html {
  font-size: 12pt;
  & * {
    margin      : 0;
    padding     : 0;
    box-sizing  : border-box;
    font-family : Montserrat;
  }
}    

 body:after {
  content: 
    url('../images/tl.jpg')
    url('../images/bed01.jpg')
    url('../images/fed01.jpg')
    url('../images/pm.jpg')
    url('../images/fed02.jpg')
    url('../images/bed02.jpg');
    display: none;
 }

